.contactsBlock {

  .contactsContainer {
    flex-direction: column;
    padding-bottom: 113px;

    &::before {
      content: "";
      background: #121415;
      width: 100%;
      height: 1px;
      margin: 33px 0 64px 0;
    }

    .contactForm form {
      min-height: 300px;
      min-width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    }

    .contactForm textarea,
    input {
      width: 100%;
    }


  }

  & button {
    align-items: center;
    font-weight: bold;
    font-size: large;
    height: 50px;
    max-width: 150px;
    width: 100%;
  }

}