.project{display:flex;flex-direction:column;padding:30px;max-width:400px;background:linear-gradient(145deg, #1e2024, #23272b);box-shadow:10px 10px 19px #1c1e22,-10px -10px 19px #262a2e;border-radius:20px;margin-top:50px;transition:background .5s ease-in-out}.project:hover{background:#191c20}.project:hover>.iconAndImg{transform:scale(1.025)}@media screen and (max-width: 399px){.project{max-width:370px}}@media screen and (max-width: 370px){.project{max-width:370px}}.project .iconWrapper{position:relative}.project .iconWrapper .icon{position:absolute;z-index:1;top:12px;right:12px;opacity:.8;pointer-events:none}.project .iconWrapper .icon:before{content:"";display:inline-block;width:20px;height:20px;background-image:url("../../../assets/svg/externalLink.svg");background-size:cover;background-position:center}.project .iconAndImg{border-radius:10px;transition:transform .5s ease-in-out}.project .iconAndImg .link{padding:0}.project .iconAndImg .link .image{max-width:340px;border-radius:10px;object-fit:cover}@media screen and (max-width: 399px){.project .iconAndImg .link .image{max-width:310px}}@media screen and (max-width: 370px){.project .iconAndImg .link .image{max-width:300px}}.project .descriptionProject .titleWrapper{padding-top:23px;padding-bottom:10px}.project .descriptionProject .titleWrapper .title{font-family:"PoppinsLight",sans-serif;font-size:12px;color:#ff014f;font-weight:100;letter-spacing:1px;text-transform:uppercase;padding:0}.project .descriptionProject .description{position:relative;font-family:"Montserrat",sans-serif;font-size:23px;line-height:34px;font-weight:600;transition:color .5s ease-in-out}.project .descriptionProject .description:hover{color:#ff014f}.project .descriptionProject .description::after{content:"🡥";bottom:15px;margin-left:15px;position:absolute;opacity:0;transition:opacity .3s ease-in-out}.project .descriptionProject .description:hover::after{animation:arrowAnimation .5s ease-in-out forwards}@keyframes arrowAnimation{0%{bottom:-10px;opacity:0;transform:translateX(-50%) scale(0.2)}100%{bottom:-5px;opacity:1;transform:translateX(-50%) scale(1)}}@media screen and (max-width: 1199px){.project .descriptionProject .description{font-size:20px;line-height:30px}}