.footerContainer {
  flex-direction: column;
  padding-bottom: 80px;

  &::before {
    content: "";
    background: #121415;
    width: 100%;
    height: 1px;
    margin: 33px 0 67px 0;
  }

  .icons {
    background-color: antiquewhite;
    width: 30%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .icon {
      display: flex;
      flex-wrap: wrap;
      max-height: 60px;
      max-width: 60px;
    }
  }
}